.container {
  margin: 20px auto;
  max-width: 1000px;
  padding: 0;
}


.form-control {
  height: 25px;
  width: 150px;
  border: none;
  border-radius: 0;
  font-weight: 800;
  padding: 0 0 5px 0;
  background-color: transparent;
  box-shadow: none;
  outline: none;
  border-bottom: 2px solid #ccc;
  margin: 0;
  font-size: 14px;
}

.form-control:focus {
  box-shadow: none;
  border-bottom: 2px solid #ccc;
  background-color: transparent;
}

.form-control2 {
  font-size: 14px;
  height: 20px;
  width: 55px;
  border: none;
  border-radius: 0;
  font-weight: 800;
  padding: 0 0 5px 0;
  background-color: transparent;
  box-shadow: none;
  outline: none;
  border-bottom: 2px solid #ccc;
  margin: 0;
}

.form-control2:focus {
  box-shadow: none;
  border-bottom: 2px solid #ccc;
  background-color: transparent;
}

.form-control3 {
  font-size: 14px;
  height: 20px;
  width: 30px;
  border: none;
  border-radius: 0;
  font-weight: 800;
  padding: 0 0 5px 0;
  background-color: transparent;
  box-shadow: none;
  outline: none;
  border-bottom: 2px solid #ccc;
  margin: 0;
}

.form-control3:focus {
  box-shadow: none;
  border-bottom: 2px solid #ccc;
  background-color: transparent;
}

p {
  margin: 0;
}

img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.text-muted {
  font-size: 10px;
}

.textmuted {
  color: #6c757d;
  font-size: 13px;
}

.fs-14 {
  font-size: 14px;
}

.btn.btn-primary {
  width: 100%;
  height: 55px;
  border-radius: 0;
  padding: 13px 0;
  background-color: black;
  border: none;
  font-weight: 600;
}
.rentalDate, 
.returnDate, 
.actualReturnDate{
  margin: 10px;
  label{
    font-family: 'Exo 2' sans-serif;
    color: white;
    margin-right: 10px;
  }
}
.btn.btn-primary:hover .fas {
  transform: translateX(10px);
  transition: transform 0.5s ease
}


.fw-900 {
  font-weight: 900;
}

::placeholder {
  font-size: 12px;
}

.ps-30 {
  padding-left: 30px;
}

.h4 {
  font-family: 'Work Sans', sans-serif !important;
  font-weight: 800 !important;
}


