.register{
background-color: #76767a;

  .top{
    padding: 20px 50px;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    &__button{
      font-size: 20px;
      color: white;
      background-color: rgb(146, 140, 140);
      border-radius: 5px;
      border: none;
      font-weight: 600;
      padding: 15px 20px;
      cursor: pointer;
      box-shadow:  0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
      a{
        text-decoration: none;
        color: white;
      }
    }
  }

  .container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    &__title{
      color: white;
    }

    p, div {
      font-family: Lato;
    }
    
    .wrapper {
      height: 97vh;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    
    .form-wrapper {
      display: flex;
      flex-direction: column;
      width: 280px;
      max-width: 80%;
      min-width: 100px;
      min-height: 400px;
      padding: 20px 40px;
      border-radius: 6px;
      box-shadow: 0px 8px 36px #222;
      background-color: rgba(0, 0, 0, 0.3);
      z-index: 2;
    }
    
    .form-wrapper > h2 {
      display: flex;
      justify-content: center;
      font-family: "Segoe UI", "Ubuntu", "Roboto", "Open Sans", "Helvetica Neue", sans-serif;
      font-size: 2em;
      font-weight: lighter;
      margin-top: 0.25em;
      color: #222;
    }
    
    form {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
    }
    
    .info {
      padding-bottom: 1em;
      padding-left: 0.5em;
      padding-right: 0.5em;
    }
    
    label {
      margin-bottom: 0.5em;
      color: #ece2e2;
      font-weight: lighter;
    }
    
    input {
      padding: 10px 10px;
      border-radius: 5px;
      outline: none;
      border: 1px solid #d6d1d5;
    }
    
    input::placeholder {
      font-size: 1.2em;
      font-weight: lighter;
      color: #bbb;
    }
    
    button {
      min-width: 100%;
      cursor: pointer;
      margin-right: 0.25em;
      margin-top: 0.5em;
      padding: 	0.938em;
      border: none;
      border-radius: 4px;
      background-color: #76767a;
      color: #100e0e;
      font-weight: 900;
    }
    button:hover {
      background-color: #4A4E69;
      color: #fefefe;
    }
    
    input.error {
      border: 1px solid #EADAE4;
    }
    
    .error {
      color:#db2269;
      font-size: 18px;
      display: relative;
    }
    
    .fullName {
      margin-right: 1%;
    }
    
    .firstName,
    .lastName,
    .email,
    .password {
      display: flex;
      flex-direction: column;
      margin-bottom: 15px;
      color: white;
    }
    
    .fullName {
      width: 100%;
    }
    
    .email,
    .password {
      width: 100%;
    }
    
    .submit {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
    }
  

   
  }
  .car_img{
    opacity: 2;
    animation: 2s anim-line ease-out ;
    left: 0;
    position: absolute;
    bottom: 0px;
    width: 700px;
    height: 400px;
  }
  @keyframes anim-line {
    0% {
      opacity: 0;
      transform: translateX(100%);
    }
    20% {
      opacity: 0;
    }
    50% {
      opacity: 1;
      transform: translateX(80%);
    }
    100% {
      opacity: 1;
      transform: translateX(-100%);
    }
  }
}